@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
*{font-family: 'Roboto', sans-serif;font-size: 18px;}
body{display: flex;justify-content: center;align-items: center;}
.container{top: 50%;left: 50%;position: absolute;-webkit-transform: translate(-50%, -50%);transform: translate(-50%, -50%);margin: 0 auto;width: 256px;text-align: center;background: #101116;border: 10px solid #101116;border-radius: 5px;}
.keypad{display: grid;grid-template-columns: repeat(4, 1fr);grid-auto-rows: minmax(60px, auto);}
input[type="text"]{height: 75px;width: 252px;background: transparent;border: 0;color: #fff;outline: 0;text-align: right;font-size: 25px;font-weight: 700;letter-spacing: 1px;}
#clear{grid-column: 1/3;grid-row: 1;color: #262834}
#result{grid-column: 3/5;grid-row: 5;color: #262834}
#backspace{color: #262834}
button{margin: 5px;border: none;border-radius: 5px;cursor: pointer;background: #262834;color: #fff;font-weight: 500;}
.highlight{background: #56cbdb;}


